.wp-block-image {
  margin-left: 0;
  margin-right: 0;
}

.usa-overlay.is-visible ~ .usa-header {
  .usa-menu-btn {
    background-color: #61366F;
    color: #ffffff;
  }
}

.usa-menu-btn {
  color: #61366F;
  background-color: transparent;
  cursor: pointer;

  svg {
    height: 18px;
    width: 20px;
    margin-right: 7px;
  }

  path {
    fill: #61366F;
  }

  &:hover {
    background-color: #61366F;
    color: #ffffff;

    path {
      fill: #ffffff;
    }
  }
}

.usa-nav.padding-0 {
  padding: 0 !important;
}

.usa-prose {
    font-size: 1.125rem;
}

.usa-button--white:hover {
  background-color: #efefef;
}

.font-sans-5xl {
  font-size: 3rem;
}

.bg-ct-blue {
  background-color: #09538B;
}

.bg-footer {
  background-color: #053756;
}

.text-ct-blue {
  color: #09538B;
}

.usa-sidenav {
  padding-left: 1.75rem;
}

.usa-accordion {
  .widget-title {
    margin: 0;
    background-image: url(../img/plaid.png);
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    padding: 0.5rem 1rem;
    line-height: 1.1;
  }
}

@media (min-width: 1024px) {
  .usa-accordion {
    .widget-title {
      border-top-left-radius: 6px;
      border-top-right-radius: 6px;
    }
  }
}

.usa-sidenav .usa-current:after {
  left: -0.75rem !important;
}

.usa-nav__primary {
  button,
  a.usa-nav__link {
    padding-left: 0 !important;
  }
}

.usa-sidenav__item {
  button,
  a {
    padding-left: 0;
  }
  
  &.has-current {
    & > button {
      font-weight: bold;
    }
  }

  &.is-active {
    & > button {
      background-image: url(../img/angle-arrow-up-primary.svg);
      font-weight: bold;
    }
  }

  .usa-sidenav__sublist {
    margin-left: 1.5rem;

    .usa-sidenav__item {
      border-top: none;
      margin-left: -0.75rem;
    }

    .usa-current {
      background: #ffffff;
    }

    button,
    a {
      padding-left: 0.75rem;
    }
  }

  button {
    background-color: transparent;
    border: none;
    appearance: none;
    display: block;
    cursor: pointer;
    width: 100%;
    text-align: left;
    padding: 0.5rem 0;
    color: #565c65;
    background-image: url(../img/angle-arrow-down.svg);
    background-position: right center;
    background-size: 15px;
    background-repeat: no-repeat;
    padding-right: 2rem;
  }
}

@media (min-width: 64em) {
  .font-sans-5xl {
    font-size: 4.375rem;
  }
}

.b {
    left: 50%;
    position: relative;
    transform: translate(-50%,0);
    width: 100vw;

    &--wave {
      background-position: top right;
      background-size: cover;
      background-repeat: no-repeat;
      width: 75%;
      min-height: 250px;
      display: flex;
      align-items: center;

      @media (min-width: 64em) {
        width: 75%;
        min-height: 400px;
      }

      @media (min-width: 80em) {
        width: 70%;
        min-height: 600px;
      }
    }

    &--hero {
      background-position: top right;
      background-size: auto 100%;
      background-repeat: no-repeat;
      width: 100%;
      min-height: 250px;

      @media (min-width: 64em) {
        min-height: 400px;
      }
    }

    .text-white {
        a {
            color: #ffffff !important;
        }
    }

    &--cta {
        a {
            color: #ffffff;

            &:hover {
                text-decoration: none;
            }
        }

        p {
            margin-bottom: 0;
        }
    }

    &--commissioner {
        background-color: #09538B;
    }
}

.usa-prose,
.breadcrumbs {
  a:link,
  a:visited,
  a:hover {
    @include u-color('blue-50');
  }

  a:hover {
    @include u-text('no-underline');
  }
}

.usa-dark-background,
.usa-hero__callout {
  @include u-bg('blue-50');
}

.wp-block-table {
  @extend .usa-table;
  @include u-width('full');
}

.wp-block-simple-alert {
  @include u-bg('gold-10v');
  @include u-padding('2');
  @include u-margin-top('2');
}

.bg-violet-70v {
  @include u-bg('violet-70v');
}

.sf-field-submit input {
  margin-top: 0;
}

