    &::after {
        display: inline-block;
        content: '';
        background-image: url("data:image/svg+xml;base64,");
        background-position: center;
        background-repeat: no-repeat;
        background-size: 15px 15px;
        width: 15px;
        height: 15px;
        vertical-align: middle;
        margin-left: 4px;
    }
}

