/*
----------------------------------------
Gravity Forms
----------------------------------------
*/
.gform_body .ginput_container .gfield_label {
    @extend .usa-label;
}

.gform_body .ginput_container .ginput_container_text {
    input {
        @extend .usa-input;
    }
}

.gform_footer {
    padding-top: 1rem;
}

.gform_button {
    @extend .usa-button;
}

.gform_body .ginput_container .ginput_container_checkbox {
    margin-top: 0.5rem;
}

.gform_body .ginput_container .gfield_checkbox {
    label {
        @extend .usa-checkbox__label;
        font-size: 1rem;
    }
    input {
        @extend .usa-checkbox__input;
    }
}

/*
----------------------------------------
Search filter
----------------------------------------
*/

.searchandfilter label {
  @include u-display('block');
}

.searchandfilter {
  @extend .usa-form;
}

.gform_fields,
.gform_fields ul,
.searchandfilter ul,
.sf_date_field {
  @include unstyled-list;
}

.sf-level-0 {
    margin-left: 22px !important;

    .children {
        display: none;
    }
}

.sf-input-text {
  @extend .usa-input;
}

.sf-field-submit {
  input {
    @extend .usa-button;
    margin-top: 0;
  }
}

[data-sf-field-input-type=checkbox] {
  .sf-input-checkbox {
    @extend .usa-checkbox__input;
  }

  .sf-label-checkbox {
    @extend .usa-checkbox__label;
  }
}

/*
----------------------------------------
Primary Nav Customizations
----------------------------------------
*/

@media (max-width: 64em) {
    .usa-navbar {
        .usa-logo {
            svg {
                height: 35px !important;
            }
        }
    }
}

@media (min-width: 64em) {
  .usa-header--extended .usa-navbar,
  .usa-header--extended .usa-nav__inner {
    max-width: none !important;
  }

  .usa-header--extended .usa-nav__primary {
    margin-left: -0.5rem !important;
  }

  .usa-nav__secondary {
      display: flex;
      margin-top: 0 !important;
      align-items: center;

      .usa-search {
          margin-top: 0 !important;
          display: flex;
          align-items: center;
      }

      .usa-nav__link {
          @include u-font('sans', 'sm');
      }
  }
}

.usa-nav {
    .usa-nav__primary>.usa-nav__primary-item>a {
        @include u-color('base-darkest');
    }

    .usa-nav__primary {
        .usa-nav__submenu {
          z-index: 1000;
          list-style: none;
        }

        button.usa-nav__link[aria-expanded=true] {
          z-index: 1001;
        }
    }

    .usa-nav__secondary {
        .usa-nav__link {
          @include u-color('base-dark');
          @include u-font('sans', 'xs');
        }

        .usa-nav__primary>.usa-nav__primary-item>a {
          @include u-color('base-dark');
        }
    }
}

.usa-nav__secondary {
  li {
      list-style: none;
  }

  .usa-nav__link {
    @extend .border-transparent;
    @extend .border-x;
    @extend .border-top;
    position: relative;
    z-index: 100;
    white-space: nowrap;
    @include u-color('base-darkest');
    @include u-font('sans', 'md');
  }

  button.usa-nav__link[aria-expanded=true] {
    @extend .border-base-lighter;
    @include u-color('base-darkest');
    @include u-bg('white');
    background-image: url(../img/angle-arrow-up-primary.svg);
  }

  .usa-nav__submenu {
    margin-top: -1px;
    z-index: 1;
    @include u-bg('white');
    @include u-padding(0);
    @extend .border;
    @extend .border-base-lighter;

    .usa-translate__power {
      @include u-padding-y(1.5);
      @include u-padding-x(2);

      svg {
        height: 27px;
        width: 58.62px;
      }
    }

    .usa-nav__submenu-item {
      @include u-margin(0);

      a {
        @include u-color('base');
        @include u-display('block');
        @include u-padding-y(1.5);
        @include u-padding-x(2);

        &:hover {
          @include u-color('white');
          @include u-bg('primary-dark');
          @include u-padding-y(1.5);
          @include u-padding-x(2);
        }
      }
    }
  }
}

.gsc-search-box {
  margin: 0 !important;
  max-width: 250px;
}

.usa-search--big {
  display: inline-block; 
  
  .gsc-search-box {
    max-width: 400px;
  }
}

.gsc-control-cse {
  padding: 0 !important;
}

.usa-search--cse > div {
  width: 100%;
  min-width: 208px;
}

.gsc-input {
  padding: 0 !important;
}

.gsc-input-box {
  @extend .usa-input;
  @extend .width-auto;
  margin: 0;

  .gsib_a {
    padding: 0;
  }
}

button.gsc-search-button {
  margin: 0;
  @extend .usa-button;
  @include u-bg('white');
  padding: 11px 12px;
  border: 1px solid #dcdee0;
  border-left: none;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;

  &:hover {
    border: 1px solid #dcdee0;
    border-left: none;
    @include u-bg('white');
  }

  svg {
    path {
      fill: #565c65;
    }
  }
}

/*
----------------------------------------
Footer
----------------------------------------
*/

.footer-list {
  @include u-font('sans', '2xs');
  
  ul {
    @extend .usa-list;
    @extend .usa-list--unstyled;
  }

  li {
    &:not(:first-child) {
      @include u-margin-top('2');
    }
  }

  a {
    @include u-color('white');
    @include u-text('no-underline');

    &:hover {
      @include u-text('underline');
    }
  }

  .notranslate {
    @include u-display('flex');

    li {
      @include u-margin-top('0');
      @include u-margin-right('2');
    }
  }
}

/*
----------------------------------------
Scroll to top
----------------------------------------
*/
  
#scrolltotop {
  @include u-shadow(3);
  @include u-z(100);
  @include u-position('fixed');
  @include u-text('uppercase');
  @include u-right(2);
  @include u-bottom(3);

  &[hidden] {
    display: none;
  }
}

/*
----------------------------------------
Translations
----------------------------------------
*/

#google_translate_element {
  width:300px;
  float:right;
  text-align:right;
  display:block
}

.goog-te-banner-frame.skiptranslate,
.activity-root,
.status-message,
.started-activity-container { 
  display: none !important;
} 

body { 
  top: 0px !important; 
}

#goog-gt-tt, 
.goog-tooltip skiptranslate {
  display: none !important; 
  top: 0px !important; 
}

.usa-nav__translate {
  display: flex;
  align-items: center;

  &:hover {
    .usa-svg__fill {
      fill: color($theme-color-primary);
    }
  }
  
  svg {
    height: 16px;
    width: 16px;
    margin-right: 5px;
  }
}



@media print {
    * {
        background: #ffffff !important;
        border-color: #000000 !important;
        color: #000000 !important;
    }

    #logo g {
        fill: #000000;
    }

    .usa-menu-btn,
    .usa-accordion__heading,
    .footer-list,
    .notranslate,
    .usa-sidenav,
    #scrolltotop,
    header.usa-dark-background,
    .usa-nav {
        display: none;
    }
}

.wp-block-embed-youtube {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px; height: 0; overflow: hidden;

  iframe,
  object,
  embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}